import { Component, Vue } from 'vue-property-decorator'
import Card from '../components/Card.vue'
import Question from '../components/Question.vue'

@Component({
  name: "Quiz",
  components: {
    Card,
    Question
  }
})
export default class Quiz extends Vue {
  questions?: Array<object> = []
  answers: Array<Answer> = []
  got = false
  finished = false
 
  test(data: [number, Answer]) {
    this.answers[data[0]] = data[1]
    this.finished = this.questions ? this.answers.filter(_ => _).length == this.questions.length: false
  }

  async created() {
    this.questions = (await fetch('https://phase1-cs.herokuapp.com/getSurvey').then(_ => _.json())).questions as Array<object>
    this.questions = [...this.questions]
    this.got = true
  }

  submit() {
    fetch('https://phase1-cs.herokuapp.com/getCourses', {method: 'POST', body: JSON.stringify({answers: this.answers})}).then(_ => _.json()).then(_ => alert(_['courses'].join('\n')))
  }
}

interface Answer {
  type: string, 
  id: number, 
  answer: Array<number>
}
